.textContainer {
  width: 250px;
  height: max-content;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: #333639;
  top: 0;
  z-index: 2;
}
.hover:hover {
  background-color: black;
  transition: 0.4s;
}
.triangleDoc {
  position: absolute;
  left: 265px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 20px solid transparent;
  border-top: 70px solid #333639;
  transform: rotate(-40deg);
  z-index: -1;
}
.trianglePat {
  position: absolute;
  left: -10px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 20px solid transparent;
  border-top: 70px solid #333639;
  transform: rotate(30deg);
  z-index: -1;
}
.current {
  background-color: #333639;
}
.typing-demo {
  /* animation: typing 2s steps(22); */
  /* white-space: nowrap; */
  overflow: hidden;
  font-family: monospace;
  font-size: 18px;
}

@keyframes typing {
  0% {
    width: 0;
  }
  100% {
    width: 50px;
  }
}

/* @keyframes typing {
  from {
    width: 0;
  }
} */
@media (max-width: 1000px) {
  .textContainer {
    width: 330px;
  }
  .hover:hover {
    background-color: transparent;
  }
}
