.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  gap: 80px;
  position: relative;
  overflow: hidden;
  background-image: url("./components/Image/doctor_room.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}
.menuButtons {
  position: absolute;
  background-color: #8a960e;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  top: 50px;
  left: 50px;
  gap: 10px;
  z-index: 2;
}
.menuButtons button {
  cursor: pointer;
  border: none;
  font-size: 24px;
  padding: 4px 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
  border-radius: 5px;
}
.dialogInputs {
  position: absolute;
  top: 0;
}

.doctorPerson {
  width: max-content;
  position: absolute;
  bottom: 0;
  margin-left: 600px;
}
.doctorPerson img {
  width: 250px;
}
.patientPerson {
  width: max-content;
  position: absolute;
  bottom: -45px;
  margin-right: 700px;
}
.patientPerson img {
  width: 530px;
}
.dialogs {
  position: absolute;
  background-color: #252525c2;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 90vh;
  transition: 1s;
}
.dialogsContent {
  overflow-y: scroll;
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 15px;
}
.dialogsContent::-webkit-scrollbar {
  width: 5px;
}

.dialogsContent::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
  border-radius: 10px;
}

.dialogsContent::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
}
.openDialogsButton {
  position: absolute;
  width: 80px;
  height: 20px;
  border: none;
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
  transition: 1s;
}
.mouse {
  animation: talk 0.3s linear infinite;
}
@keyframes talk {
  0% {
    height: 0;
  }
  100% {
    height: 10px;
  }
}
@media (max-width: 1700px) {
  .menuButtons {
    opacity: 0.8;
  }
  .patientPerson {
    margin-right: 200px;
  }
}

@media (max-width: 1000px) {
  .App {
    flex-direction: column;
    gap: 50px;
    height: 100vh;
  }
  .pullQuestions {
    overflow-y: scroll;
    max-height: 50vh;
  }
  .logo {
    display: none;
  }
}
