.opacityFone1 {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}
.opacityFone1 button {
  max-width: 100px;
}
.modalContent1 {
  width: 60%;
  padding: 20px 30px;
  background-color: rgba(0, 0, 0, 0.85);
  border: 1px solid wheat;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 5;
  min-height: 30vh;
  max-height: max-content;
  padding: 50px 100px;
}

.sectionResultLeft {
  overflow-y: scroll;
  padding-right: 50px;
}
.sectionResultLeft::-webkit-scrollbar {
  width: 5px;
}

.sectionResultLeft::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
  border-radius: 10px;
}

.sectionResultLeft::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
}
.mainResult {
  display: flex;
  justify-content: space-between;
}
.sectionResultRight {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.percent {
  position: relative;
  width: 280px;
  height: 280px;
  margin: 15px auto 0 auto;
}

.percent svg {
  position: relative;
  width: 280px;
  height: 280px;
}

.percent svg circle {
  width: 280px;
  height: 280px;
  fill: none;
  stroke-width: 18;
  stroke: #000;
  transform: translate(10px, 10px);
  stroke-dasharray: 691;
  /* stroke-dashoffset: 520; */
}

.percent svg circle:nth-child(1) {
  stroke-dashoffset: 0;
  stroke: #d9e836;
}
.percent svg circle:nth-child(2) {
  stroke: rgba(0, 0, 0, 0.8);
}
